.footer {
	padding-top: toRem(45);
	padding-bottom: toRem(45);
	background-color: var(--brand-color);
	// .footer__container

	&__container {
		display: grid;

		@media (min-width: $mobile) {
			grid-template-columns: 39.4% 1fr;
			@include adaptiveValue("gap", 70, 30, 0, $containerWidth, 767.98);
		}
		@media (max-width: $mobile) {
			gap: toRem(20);
		}
	}

	// .footer__content

	&__content {
		@media (min-width: $mobile) {
			padding-top: toRem(15);
		}
		@media (max-width: $mobile) {
			text-align: center;
		}
		&.content {
			.content__title {
				&:not(:last-child) {
					margin-bottom: toRem(27);
					@media (max-width: $tablet) {
						margin-bottom: toRem(20);
					}
				}
			}

			.content__text {
				line-height: 1.5;
				@media (min-width: $mobile) {
					&:not(:last-child) {
						margin-bottom: toRem(20);
					}
				}
			}

			@media (max-width: $mobile) {
				form {
					display: none;
				}
			}
		}
	}
}
