.form {
	display: grid;
	gap: toRem(25);

	// .form__row

	&__row {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: toRem(30);
		align-items: end;
		@media (max-width: $tablet) {
			gap: toRem(15);
		}
		&_wide {
			@media (max-width: $mobileSmall) {
				grid-template-columns: 1fr;
				gap: toRem(20);
			}
		}
	}

	// .form__line

	&__line {
	}

	&__label {
		display: block;
		font-size: toRem(18);
		font-weight: 600;
		margin-bottom: toRem(10);
	}

	// .form__input

	&__input {
	}

	// .form__send

	&__send {
		@media (min-width: $mobile) {
			margin-right: auto;
		}
		&.button {
			&_black-default {
				border-radius: toRem(8);
				text-transform: uppercase;
				font-size: toRem(14);
			}
		}
	}
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}
.input {
	border-radius: toRem(8) !important;
	width: 100%;
	display: block;
	font-size: toRem(16);
	padding: 0 toRem(16);
	height: toRem(58);
	background-color: var(--white-color);
	transition: all 0.3s;
	color: var(--primary-color);
	outline: toRem(1) solid var(--white-color);
	&::placeholder {
		color: #94a3b8;
	}
	&:focus {
		outline-color: var(--primary-color);
	}
}
.input[placeholder]:not([data-placeholder-nohiden]) {
	&::placeholder {
		transition: opacity 0.3s;
	}
	&:focus::placeholder {
		opacity: 0;
	}
}

textarea.input {
	resize: vertical;
	padding: toRem(16);
	min-height: toRem(128);
}
