.testimonials {
	padding-top: toRem(36);
	padding-bottom: toRem(51);
	background-color: var(--brand-color);
	// .testimonials__container

	&__container {
	}

	// .testimonials__content

	&__content {
		&:not(:last-child) {
			margin-bottom: toRem(40);
			@media (max-width: $mobile) {
				margin-bottom: toRem(20);
			}
		}
		&.content {
			.content__title {
			}
		}
	}

	// .testimonials__body

	&__body {
		min-width: 0;
		position: relative;
	}

	// .testimonials__slider

	&__slider {
	}

	// .testimonials__wrapper

	&__wrapper {
	}

	// .testimonials__pagination

	&__pagination {
	}
}

//========================================================================================================================================================

.testimonial-slide {
	// .testimonial-slide__body

	&__body {
		display: grid;
		gap: toRem(30);
		@media (min-width: $mobile) {
			grid-template-columns: percent(447, $containerWidth) 1fr;
		}
		@media (max-width: $mobile) {
			gap: toRem(30);
		}
	}

	// .testimonial-slide__profile

	&__profile {
	}

	// .testimonial-slide__info

	&__info {
		border-radius: toRem(5);
		display: flex;
		flex-direction: column;
		justify-content: center;
		background-color: var(--white-color);
		padding: toRem(34) toRem(67) toRem(55) toRem(56);
		@media (max-width: $tablet) {
			padding: toRem(30);
		}
	}

	// .testimonial-slide__quote

	&__quote {
		line-height: 186.72%;
		letter-spacing: toRem(0.24);
		flex: 1 1 auto;
		display: flex;
		flex-direction: column;
		gap: toRem(55);
		color: var(--grey-color-300);
		&:not(:last-child) {
			margin-bottom: toRem(73);

			@media (min-width: $mobile) and (max-width: $tablet) {
				margin-bottom: toRem(30);
				gap: toRem(25);
			}
		}
		&::before {
			content: "";
			width: toRem(68);
			flex: 0 0 toRem(68);
			height: toRem(68);
			background: url("@img/icons/quote.png") 0 0 / contain no-repeat;
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(16);
		}
	}

	// .testimonial-slide__footer

	&__footer {
	}

	// .testimonial-slide__rating

	&__rating {
	}
}

.profile {
	border-radius: toRem(5);
	display: flex;
	flex-direction: column;
	gap: toRem(35);
	justify-content: center;
	align-items: center;
	background-color: var(--white-color);
	text-align: center;
	padding: toRem(64) toRem(15) toRem(55) toRem(15);
	@media (max-width: $mobile) {
		gap: toRem(30);

		padding: toRem(26) toRem(15) toRem(40) toRem(15);
	}
	// .profile__avatar

	&__avatar {
		flex: 0 0 toRem(220);
		width: toRem(220);
		height: toRem(220);
		border-radius: 50%;
		border: toRem(10) solid var(--brand-color);
		overflow: hidden;
		@media (max-width: $mobileSmall) {
			flex: 0 0 toRem(157);
			width: toRem(157);
			height: toRem(157);
		}
	}

	// .profile__name

	&__name {
		font-size: toRem(30);
		font-weight: 600;
		color: var(--brand-color);
		letter-spacing: toRem(0.3);
		@media (max-width: $mobileSmall) {
			font-size: toRem(22);
		}
	}
}

.rating {
	display: flex;
	position: relative;
	align-items: center;
	gap: toRem(44);
	&::after {
		content: "";
		height: toRem(1);
		background-color: var(--grey-color-300);
		width: 55.907514%;
		position: relative;
		top: toRem(3);
	}
	@media (max-width: $mobile) {
		gap: toRem(10);
	}
	&__wrapp {
		display: flex;
		align-items: center;
		gap: toRem(3);
	}
	// .rating__item

	&__item {
		flex: 0 0 toRem(20);
		width: toRem(20);
		height: toRem(20);
		@media (max-width: $mobileSmall) {
			flex: 0 0 toRem(15);
			width: toRem(15);
			height: toRem(15);
		}
		img {
			max-width: 100%;
		}
	}
}

.pagination {
	display: flex;
	justify-content: center;
	align-items: center;
	gap: toRem(27.5);
	justify-content: end;
	margin-top: toRem(47);

	@media (min-width: $mobile) {
		@include adaptiveValue("padding-right", 290, 190, 0, $containerWidth, 767.98);
	}
	@media (max-width: $mobile) {
		margin-top: toRem(20);
		justify-content: center;
	}
	@media (max-width: $mobile) {
		gap: toRem(14);
	}
	.swiper-pagination-bullet {
		flex: 0 0 toRem(27);
		width: toRem(27);
		height: toRem(27);
		border-radius: 50%;
		border: toRem(2) solid var(--white-color);
		transition: all 0.3s;
		cursor: pointer;
		display: flex;
		justify-content: center;
		align-items: center;
		&::before {
			content: "";
			flex: 0 0 toRem(14);
			width: toRem(14);
			height: toRem(14);
			background-color: var(--white-color);
			border-radius: 50%;
			transition: all 0.3s;
		}
	}
	.swiper-pagination-bullet-active {
		border-color: var(--primary-color);
		&::before {
			transform: scale(1);

			background-color: var(--primary-color);
		}
	}
}
