.outpatient-treatment {
	padding-top: toRem(60);
	padding-bottom: toRem(60);
	@media (max-width: $mobile) {
		padding-top: toRem(30);
		padding-bottom: toRem(86);
	}
	background-color: var(--green-color-100);
	// .outpatient-treatment__container

	&__container {
		text-align: center;
	}

	// .outpatient-treatment__header

	&__header {
		max-width: toRem(750);
		margin: 0 auto;
		&:not(:last-child) {
			margin-bottom: toRem(36);
			@media (max-width: $mobile) {
				margin-bottom: toRem(25);
			}
		}
		&.content {
			.content__title {
			}

			.content__text {
			}
		}
	}

	// .outpatient-treatment__content

	&__content {
		&.content {
			.content__title {
			}

			.content__text {
			}
		}
	}
}
.table {
	overflow-x: auto;
	&:not(:last-child) {
		margin-bottom: toRem(46);
		@media (max-width: $mobile) {
			margin-bottom: toRem(25);
		}
	}
	table {
		width: 100%;
		border-collapse: collapse;
		text-transform: capitalize;
	}
	thead {
		border-bottom: toRem(10) solid var(--white-color);
		tr {
			th {
				&:not(:last-child) {
					border-right: toRem(22) solid var(--white-color);
					@media (max-width: $mobile) {
						border-right-width: toRem(7);
					}
				}
				background-color: var(--brand-color);
				font-weight: 600;
				font-size: toRem(22);
				text-align: center;
				line-height: 1.6;
				height: toRem(95);
				padding: toRem(20);
				@media (max-width: $tablet) {
					padding: toRem(10);
				}
				@media (max-width: $mobile) {
					font-size: toRem(15);
				}
				@media (max-width: $mobileSmall) {
					text-align: left;
					padding-left: toRem(38);
				}
			}
		}
		// border-bottom: toRem(10) solid var(--green-color-100);
	}

	tbody {
		font-size: toRem(18);
		font-weight: 300;
		th:first-child,
		td:first-child {
			@include adaptiveValue("padding-left", 111, 10);
		}
		tr {
			&:nth-child(odd) {
				td {
					background-color: var(--yellow-color-100);
				}
			}
			&:nth-child(even) {
				td {
					background-color: var(--yellow-color-200);
				}
			}
			td {
				line-height: 1.5;
				padding: toRem(2) toRem(27);
				width: toRem(400);
				font-size: toRem(18);
				min-width: toRem(224);
				height: 95px;
				text-align: left;
				&:not(:last-child) {
					border-right: toRem(22) solid var(--white-color);
					@media (max-width: $mobile) {
						border-right-width: toRem(7);
					}
				}
				border-bottom: toRem(10) solid var(--white-color);
				@media (max-width: $tablet) {
					padding: toRem(10);
				}
				@media (max-width: $mobile) {
					font-size: toRem(15);
				}
				@media (max-width: $mobileSmall) {
					padding-left: toRem(38);
				}
			}
		}
	}
}
