.about {
	background-color: var(--brand-color);
	padding-bottom: toRem(92);
	@media (max-width: $mobile) {
		padding-top: toRem(25);
		padding-bottom: toRem(25);
	}

	// .about__container

	&__container {
	}

	// .about__body

	&__body {
		padding: toRem(36) toRem(47);
		background-color: var(--white-color);
		border-radius: toRem(5);
		position: relative;
		z-index: 5;
		@media (min-width: $mobile) {
			margin-top: toRem(-87);
		}
		@media (max-width: $mobile) {
			padding: toRem(20) toRem(15);
		}
	}

	// .about__header

	&__header {
		&:not(:last-child) {
			margin-bottom: toRem(40);
			@media (max-width: $mobile) {
				margin-bottom: toRem(18);
			}
		}
		&.content {
			.content__title {
			}
		}
	}

	// .about__list

	&__list {
		display: grid;
		gap: toRem(57);
		@media (min-width: $mobile) {
			grid-template-columns: percent(422, 1138) 1fr;
		}
		@media (max-width: $mobile) {
			gap: toRem(25);
		}
	}

	// .about__item

	&__item {
	}
}

.item-about {
	display: flex;
	flex-direction: column;
	// .item-about__icon

	&__icon {
		flex: 0 0 toRem(144);
		width: toRem(144);
		height: toRem(144);
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: var(--brand-color);
		border-radius: toRem(5);
		&:not(:last-child) {
			margin-bottom: toRem(28);
		}
		img {
			width: toRem(97);
			height: toRem(97);
		}
	}

	// .item-about__text

	&__text {
		line-height: 1.3;
		letter-spacing: toRem(0.24);
		@media (max-width: $mobileSmall) {
			font-size: toRem(16);
		}
	}
	@media (max-width: $mobile) {
		align-items: center;
		text-align: center;
	}
}
