body {
	// The scroll is blocked

	// The site is downloaded
	.loaded & {
	}
}
.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	@supports (overflow: clip) {
		overflow: clip;
	}
	// We press Footer
	> main {
		flex: 1 1 auto;
	}
	// Fix for sliders
	> * {
		min-width: 0;
	}
}

//========================================================================================================================================================

.page {
	// .page__hero

	&__hero {
	}

	&__about {
	}
	&__advantages {
	}
	// .page__programs

	&__programs {
		margin-bottom: toRem(80);
		@media (max-width: $mobile) {
			margin-bottom: toRem(40);
		}
	}
}

.content {
	// .content__title

	&__title {
		@include adaptiveValue("font-size", 40, 28);
		font-weight: 700;
		line-height: 1.25;
		letter-spacing: toRem(0.4);
		&:not(:last-child) {
			margin-bottom: toRem(20);
		}
		@media (max-width: $mobileSmall) {
			&:not(:last-child) {
				margin-bottom: toRem(10);
			}
		}
		&_big {
			@include adaptiveValue("font-size", 50, 28, 0, $containerWidth, 767.98);
		}
	}

	// .content__text

	&__text {
		line-height: 1.3;
		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(16);
		}
	}

	// .content__button

	&__button {
	}
	&_center {
		text-align: center;
	}
	&_white {
		color: var(--white-color);
	}
	&_left {
		text-align: left;
	}
}

.text {
	line-height: 1.3;
	p {
		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
	}
}
.rating {
	display: flex;
	align-items: center;
	gap: toRem(5);
	position: relative;
	bottom: toRem(3);
	// .rating__item

	&__item {
		flex: 0 0 toRem(25);
		width: toRem(25);
		height: toRem(25);
		img {
			max-width: 100%;
		}
	}
}

.ibg {
	position: relative;
	img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
	}
	&_contain {
		img {
			object-fit: contain;
		}
	}
}

.sr-only {
	position: absolute;
	width: toRem(1);
	height: toRem(1);
	padding: 0;
	margin: toRem(-1);
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border-width: 0;
}
