.advantages {
	padding-top: toRem(84);
	padding-bottom: toRem(84);
	@media (max-width: $mobile) {
		padding-top: toRem(44);
		padding-bottom: toRem(44);
	}

	// .advantages__container

	&__container {
	}

	// .advantages__content

	&__content {
		margin: 0 auto;
		&:not(:last-child) {
			margin-bottom: toRem(35);
		}

		&.content {
			.content__title {
				max-width: toRem(577);
				margin: 0 auto;
				&:not(:last-child) {
					margin-bottom: toRem(22);
				}
			}

			.content__text {
				max-width: toRem(780);
				margin: 0 auto;
			}
		}
	}

	// .advantages__list

	&__list {
		display: grid;
		@include adaptiveValue("gap", 76, 20, 0, $containerWidth, 767.98);
		@media (min-width: $mobile) {
			grid-template-columns: repeat(3, 1fr);
		}
	}

	// .advantages__item

	&__item {
	}
}

.item-advantage {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	line-height: 1.3;
	// .item-advantage__icon

	&__icon {
		flex: 0 0 toRem(114);
		width: toRem(114);
		height: toRem(114);
		&:not(:last-child) {
			margin-bottom: toRem(18);
			@media (max-width: $mobileSmall) {
				margin-bottom: toRem(10);
			}
		}
	}

	// .item-advantage__body

	// .item-advantage__title

	&__title {
		font-weight: 600;
		font-size: toRem(24);

		@media (min-width: $mobile) and (max-width: $tablet) {
			font-size: toRem(20);
		}

		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
	}

	// .item-advantage__text

	&__text {
		font-size: toRem(16);
		letter-spacing: toRem(0.16);
		@media (min-width: $mobile) and (max-width: $tablet) {
			font-size: toRem(20);
		}
	}
}
