.programs {
	// .programs__container

	&__container {
	}

	// .programs__header

	&__header {
		max-width: toRem(646);
		margin: 0 auto;
		&:not(:last-child) {
			margin-bottom: toRem(64);
			@media (max-width: $mobile) {
				margin-bottom: toRem(33);
			}
		}
		&.content {
			.content__title {
			}
		}
	}
	&__image {
		@media (min-width: $mobile) {
			display: none;
		}
		@media (max-width: $mobile) {
			text-align: center;
			border-radius: toRem(10) toRem(10) 0 0;
			border: toRem(2) solid var(--primary-color);
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			width: toRem(318);
			height: toRem(318);
			margin: 0 auto;
			@media (max-width: toEm(359)) {
				width: toRem(250);
				height: toRem(250);
			}
			&:not(:last-child) {
				margin-bottom: toRem(20);
			}
			img {
				width: toRem(286);
				height: toRem(301);
				object-fit: cover;
				@media (max-width: toEm(359)) {
					width: toRem(218);
					height: toRem(233);
				}
			}
		}
	}
	// .programs__list

	&__list {
		display: grid;
		align-items: start;
		@include adaptiveValue("gap", 114, 70, 0, $containerWidth, 767.98);
		position: relative;
		@media (min-width: $mobile) {
			padding-top: toRem(35);
			padding-bottom: toRem(65);
			grid-template-columns: repeat(2, 1fr);
			&::before,
			&::after {
				content: "";
				position: absolute;
				width: 100%;
				background-color: var(--brand-color);
				width: 100vw;
				height: 100%;
			}
			&::before {
				left: 0;
			}
			&::after {
				right: 0;
			}
		}
		@media (max-width: $mobile) {
			gap: toRem(20);
		}
	}

	// .programs__item

	&__item {
	}
}

.item-program {
	display: flex;
	flex-direction: column;
	gap: toRem(33);
	justify-content: center;
	align-items: center;
	text-align: left;
	position: relative;
	z-index: 2;
	@media (max-width: $mobile) {
		text-align: center;
		gap: toRem(20);
	}
	// .item-program__text

	&__text {
		letter-spacing: toRem(0.24);
		@media (max-width: $mobileSmall) {
			font-size: toRem(16);
		}
	}
}
