.faq {
	padding-top: toRem(90);
	padding-bottom: toRem(90);
	@media (max-width: $mobile) {
		padding-top: toRem(40);
		padding-bottom: toRem(40);
	}
	// .faq__container

	&__container {
		display: grid;
		align-items: start;
		@include adaptiveValue("gap", 54, 30, 0, $containerWidth, 767.98);
		@media (min-width: $mobile) {
			grid-template-columns: percent(496, $containerWidth) 1fr;
		}
		@media (max-width: $mobile) {
			gap: toRem(20);
		}
	}

	&__body {
		display: grid;
		gap: toRem(30);
		@media (max-width: $mobile) {
			gap: toRem(20);
		}
	}

	// .faq__content

	&__content {
		@media (min-width: $mobile) {
			padding-top: toRem(45);
		}
		@media (max-width: $mobile) {
			text-align: center;
		}
	}
	&.content {
		.content__title {
			&:not(:last-child) {
				margin-bottom: toRem(22);
			}
		}
		.content__text {
		}
	}
	&__spollers {
	}

	&__image {
		padding-bottom: 131.25%;
		position: relative;
		@media (min-width: $mobile) {
			transform: translateX(-30%);
		}
		@media (max-width: $mobile) {
			padding-bottom: 104.25%;
		}
		&::before {
			content: "";
			position: absolute;
			width: 130%;
			height: 100%;
			top: toRem(10);
			left: toRem(10);
			background-color: black;
			z-index: -1;
			@media (max-width: $mobile) {
				width: 100%;
			}
		}
		img {
			@media (min-width: $mobile) {
				width: 130%;
			}
			@media (max-width: toEm(1024)) {
				object-position: left;
			}
		}
	}
}

// .faq__spollers

.spollers {
	display: grid;
	gap: toRem(20);
}
// Спойлер
.spollers__item {
}
// Заголовок спойлера
.spollers__title {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: toRem(18);
	width: 100%;
	cursor: pointer;
	text-align: left;
	padding: toRem(20) toRem(31);
	position: relative;
	list-style: none;
	@include adaptiveValue("font-size", 22, 18, 0, $containerWidth, 767.98);
	font-weight: 600;
	line-height: 1.3;
	background-color: var(--yellow-color-100);
	transition: background-color 0.3s;
	@media (max-width: $tablet) {
		padding: toRem(20);
	}
	@media (any-hover: hover) {
		&:hover {
			background-color: var(--hover--color);
		}
	}
	&::marker,
	&::-webkit-details-marker {
		display: none;
	}
	@media (max-width: $mobileSmall) {
		padding: toRem(5) toRem(10) toRem(5) toRem(10);
		gap: toRem(5);
	}
	@media (max-width: $mobileSmall) {
		font-size: toRem(13);
	}

	&._spoller-active {
		background-color: var(--brand-color);
		.spollers__icon {
			transform: scale(-1);
		}
	}
}

.spollers__icon {
	flex: 0 0 toRem(26);
	width: toRem(26);
	height: toRem(26);
	background: url("@img/icons/arrow.png") right / contain no-repeat;
	transition: transform 0.3s;
}
// Контент спойлера
.spollers__body {
	color: var(--grey-color-400);
	padding: toRem(15) toRem(30) toRem(20) toRem(30);
	background-color: var(--yellow-color-100);
	line-height: 1.5;
	@include adaptiveValue("font-size", 19, 16, 0, $containerWidth, 767.98);
	@media (max-width: $tablet) {
		padding: toRem(14) toRem(20);
	}
	@media (max-width: $mobileSmall) {
		font-size: toRem(12);
		padding: toRem(5) toRem(10);
	}
}
