.hero {
	position: relative;
	@media (max-width: $mobile) {
		display: flex;
		flex-direction: column-reverse;
		padding-top: toRem(85);
	}

	// .hero__container

	&__container {
		display: grid;
		align-items: center;
		gap: toRem(20);
		@media (min-width: $mobile) {
			min-height: 100vh;
			padding-top: toRem(120);
			padding-bottom: toRem(30);
		}
	}

	// .hero__content

	&__content {
		position: relative;
		z-index: 2;
		&.content {
			@media (min-width: $mobile) {
				color: var(--white-color);
				max-width: toRem(876);
				margin: 0 auto;
			}
			.content__title_big {
				&:not(:last-child) {
					margin-bottom: toRem(30);
					@media (max-width: $mobile) {
						margin-bottom: toRem(14);
					}
				}
			}
			.content__text {
				@include adaptiveValue("font-size", 30, 16);
				&:not(:last-child) {
					margin-bottom: toRem(44);
					@media (max-width: $mobile) {
						margin-bottom: toRem(20);
					}
				}
			}
			@media (max-width: $mobile) {
				padding-top: toRem(21);
				padding-bottom: toRem(25);
			}
		}
	}

	&__image {
		@media (min-width: $mobile) {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			object-fit: cover;
			img {
				position: absolute;
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;
				object-fit: cover;
				object-position: top;
			}
		}
		&::before {
			content: "";
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background: rgba(0, 0, 0, 0.4);
			z-index: 1;
		}
		@media (max-width: $mobile) {
			position: relative;
			img {
				max-width: 100%;
			}
		}
	}
}
